<template>
  <div :class="[{'e-vue-notecard':true},`e-vue-notecard-${type}`]" :randomNum="randomNum" :style="{'---colorBG':`var(--tileBG${randomNum})`}">
    <div class="e-vue-notecard-nav" style="">
      <div class="e-vue-notecard-nav-inner">
        <div class="e-vue-notecard-fire" style="">
          <svg style="display: inline-block;width: 30px;height: 30px;" v-if="type=='subject'">
              <use href="#svg__arrowdown" ></use>
          </svg>
          <svg style="display: inline-block;width: 30px;height: 30px;" v-else-if="type=='content'">
              <use href="#svg__circle_solid"></use>
          </svg>
          
        </div>
        <div class="e-vue-notecard-nav-content">
        </div>
      </div>
      
    </div>
    
    <router-link :to="_href"  v-if="data.title">
      <svg viewBox="0 0 50 50" style="width:100%;">
        <text :style="{'font-size': data.title.length>11?'28%':'34%'}" dominant-baseline="middle" text-anchor="middle" x="50%" y="50%">{{data.title}}</text>
      </svg>
    </router-link>
    <div class="e-vue-notecard-inc">

    </div>
  </div>
  
</template>

<script>
import { computed, ref } from 'vue'
//import { useRoute } from 'vue-router'
import EVInput from '@/components/inputs/e-v-input'




export default {
  name: 'notecard',
  components: {
    EVInput
  },
  async setup(props,context) {
    

    let aa = ref(0)
    
    let _href = `/space/${props.id}/${props.pdim1||5}/${props.pdim2||5}`
    if(props.data.type=="content"){
      _href = `/space/content/${props.id}`
    }
    //_href="/"
    aa.value = 1
    let bb = await new Promise((a)=>{
      setTimeout(function(){
        a()
      }, Math.random()*1000)
    })
    
    
    

    return{
      _href: ref(_href),
      aa: aa,
      dim1: props.pdim1,
      dim2: props.pdim2,
      type: props.data.type,
      randomNum: Math.floor(Math.random() * 3) + 1 
      // notecard_add_open: notecard_add_open,
      // tl_toggle:tl_toggle
    }
  },
  props: {
    data: Object,
    id: Number,
    pdim1: Number,
    pdim2: Number
  }
}
</script>

