<template>
  <teleport to="#pop_dialogue_holder">
    <transition name="fade" mode="out-in" v-on:enter="enter">
      <div v-if="dialogue_ref.value" class="e-pop_dialogue" ref="root">
        <div v-html="text">
        </div>
        <div class="e-pop_dialogue-text2" v-if="text2" v-html="text2" @click="text2_event" @change="text2_event">
        </div>
        
        <div class="e-pop_dialogue-text3" v-if="text3" v-html="text3" @click="text3_event" display="">
        </div>
        
        <div v-if="mode=='mode2'" @click="fn_exit" class="e-pop_exitBTN">
          Exit
        </div>
      </div>
      
    </transition>
  </teleport>
</template>

<script>

import { ref } from 'vue'
export default {
  name: 'pop_dialogue',
  props: {
    dialogue_ref: Object,
    mode: String
  },
  setup(props,context){
      const root = ref(null);

      let callback_context = {}
      const exit = ()=>{
        props.dialogue_ref.value = "false2"
        if(props.dialogue_ref.before_exit){
          props.dialogue_ref.before_exit(callback_context)
        }
      }
      const fn_exit = ()=>{
        exit()
      }
      callback_context.dialogue_ref = props.dialogue_ref
      callback_context.root = root
      callback_context.fn_exit = fn_exit
      
      let fc_enter = (mode)=>{
        switch (mode || 'mode1' ){
          case 'mode1':
            return function (el, done) {
              setTimeout(()=>{
                //props.dialogue_ref.value = "false2"
                exit()
              },2000)
            }
          case 'mode2':
            return function (el, done) {
              // nop
            }
        }
      }

      return {
        root,
        text: props.dialogue_ref.text,
        text2: props.dialogue_ref.text2,
        text2_event: function() {
          return (ev)=>{
            props.dialogue_ref.text2_event(ev, callback_context)
          }
        }(),
        text3: props.dialogue_ref.text3,
        text3_event: function() {
          return (ev)=>{
            props.dialogue_ref.text3_event(ev, callback_context)
          }
        }(),
        enter: fc_enter( props.mode ),
        fn_exit
      }
  },
  methods:{
    
  }
}
</script>


<style scoped>
.e-pop_dialogue{
  background: cornflowerblue;
  padding: 1em;
  padding-top: 1.6em;
  border-right: 10px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

</style>
<style>
.e-pop_exitBTN{
  width: max-content;
  margin-left: auto;
  margin-right: 20px;
  cursor: pointer;
  position: relative;
  padding: 3px 10px;
  border-radius: 5px;
  overflow: hidden;
  margin-top: 5px;
}
.e-pop_exitBTN:before{
  /* content:""; */
  position: absolute;
  top: 0;
  left: -20px;
  width: 10px;
  height: calc(100% + 20px);
  background: #ffffff80;
  /* transition: left 0.15s linear; */
  transform: skew(-15deg);
}
.e-pop_exitBTN:hover::before{
  content:"";
  animation: e-aaaa-transleft 4.5s infinite;
  user-select: none;
}
@keyframes e-aaaa-transleft {
  from {
    left: -20px;
  }
  15%{
    left: 100%;
  }
  to {
    left: 100%;
  }
}
</style>

