
import { reactive, toRefs } from "vue";


const stateObj = {
    temp:{},
    temptext1:"",
    pathname:"",
    header_channel:null,
    user:{displayName:null},
    userMeta:{},
    input_values:{},
    due_channel:{},
    raw_fetch:[],
    raw_channel:[],
    raw_channel_list:[],
    parent_chain:[],
    page_fetch_holder:{},
    nav_item: [
        {
            title:"title1"
        },
        {
            title:"title2"
        },
        {
            title:"title3"
        },
        {
            title:"title4"
        }
    ],
    tlHolderBusy: false,
    tl_toggle: false,
    popupInstance:{},
    metaTitle:"AtticStone",
    metaDescription:""//"Let's share the infomation with unique insight."

}
const state = reactive(stateObj);

export default function appState() {
    document._state = state
    //console.log("appState init")
    return {
        ...toRefs(state),
        utils:{
            proxy_reverse:(proxy)=>{
                return Object.entries(proxy).sort(x=>parseInt(x[0])).reverse().map(x=>x[1])
            },
            filter2space:(proxyArray)=>{
                //parent_channel['schema_hash'] = "dfjifwwwe"
                return proxyArray.filter((el) => {
                    return el.schema_hash != "dfjifwwwe"
                })
            }
        }
    }
}