<template>
  <div class="e-vue-notecard-content">
    <div class="e-vue-notecard-content-panel">
      <div class="e-vue-notecard-content-panel-state">
        <transition name="fade" mode="out-in">
          <div v-if="_saving" class="e-vue-notecard-content-panel-state-puff">
            <svg style="display: inline-block; width: 26px; height: 26px">
              <use href="#svg__puff"></use>
            </svg>
          </div>
        </transition>
      </div>
      <div class="e-vue-notecard-btn" @click="fn_save">Save</div>
      <div class="e-vue-notecard-btn" style="background:#d6cbc2"  @click="fn_delete">Delete</div>
    </div>
    <div class="e-editor-head" style=""> 
      <div class="e-editor-head-inner">
        {{content}}
      </div>
    </div>
    <div id="aaeditor"></div>
  </div>
  
  <PopDialogue :dialogue_ref="popDialogue_display"></PopDialogue>
</template>

<script>
import EditorJS from "@editorjs/editorjs";
import Header from "@editorjs/header";
import List from "@editorjs/list";
import ImageTool from "@editorjs/image";
import LinkTool from "@editorjs/link";
import Embed from "@editorjs/embed";
import Delimiter from "@editorjs/delimiter";
import CodeTool from "@editorjs/code";

import { computed, ref, onMounted, onUpdated,onUnmounted } from "vue";
import { useRoute } from "vue-router";
import router from "@/router/index";
import Controller from "../modules/controller.js";
import appState from "../modules/appState.js";
import TestComponent from "@/components/testComponent.vue";
import PopDialogue from "@/components/PopDialogue";

import schema from '@/modules/schema.js'

export default {
  name: "notecard-content",
  components: {
    TestComponent,
    PopDialogue
  },
  async setup(props, context) {
    let editor = {};
    onMounted(() => {
      editor = new EditorJS({
        holderId: "aaeditor",
        autofocus: false,
        tools: {
          code:{
            class: CodeTool,
          },
          header: {
            class: Header,
            inlineToolbar: ["link"],
            config: {
              placeholder: "Header",
            },
            shortcut: "CMD+SHIFT+H",
          },
          image:{
            class: ImageTool,
            config: {
              uploader: {
                uploadByFile(file){
                  console.log("uploadByFile")
                  let uploading_img = new Promise( (resolve, reject) =>{
                      if(!file){
                          resolve({
                            "success" : 0
                          })
                      }
                      let storageRef = firebase.storage().ref('user_uploads/'+file.name)
                      let task = storageRef.put(file)
                      let snapshot_holder = null
                      task.on("state_changed",
                          function progress(snapshot) {
                              snapshot_holder = snapshot
                              console.log("progress img")
                          },
                          function error(err) {
                              console.log("err")
                              failure('failed img')
                          },
                          function complete(x) {
                              console.log("complete img")
                              snapshot_holder.ref.getDownloadURL().then(function (downloadURL) {
                                  resolve({
                                    "success" : 1,
                                    "file": {
                                        "url" : downloadURL,
                                    }
                                  })
                              });
                          }
                      )
                  })
                  return uploading_img

                }
              },
            }
          },
          list: {
            class: List,
            inlineToolbar: true,
            shortcut: "CMD+SHIFT+L",
          },
          embed: {
            class: Embed,
            inlineToolbar: true
          },
          linkTool: LinkTool
          
        },
        data: {
          blocks: data[0]["note_data"]
        },
      });
    })
    onUnmounted(() => {
      console.log('onUnmounted')
      _appState.page_fetch_holder.value["space_list"] = null
    })
    let fn_save = () => {
      editor
        .save()
        .then((outputData) => {
          console.log("Article data: ", outputData);
          _saving.value = true;
          return controller.save_note(data[0]["d_key"], outputData.blocks);
        })
        .then(() => {
          _saving.value = false;
          console.log("Data Saved");

          popDialogue_display.value.text = "Saved."
          popDialogue_display.value.value = !popDialogue_display.value.value
        })
        .catch((error) => {
          _saving.value = false;
          console.log("Saving failed: ", error);
        });
    };
    let fn_delete = ()=>{
      controller.delete_note(data[0]["d_key"]);
    }
    let popDialogue_display = ref(new Proxy(
      {
        value:false,
        text:"Saved."
      },
      {
        set:(obj, key, value)=>{
          if(key=="value"){
            if(obj[key] == true && value=="false2"){
              obj[key] = false
              return true
            }else{
              obj[key] = value;
              return true;
            }
          }else{
            return true
          }

        }
      }
    ));

    const _appState = appState();
    const { currentRoute } = router;
    const route_params = currentRoute._value.params;
    let controller = Controller();

    let _saving = ref(false);


    let dataPipeLine = async function(pid, channel_id){ // get_noteArray, get_note_parentChain, get_channel
      let data = null
      let parent_chain = null
      let channel = null

      let _channel_id = channel_id
      // first pipeLine
      await Promise  
        .all([
          controller.get_note(pid),
          controller.get_note_parentChain(pid),
          _channel_id ? controller.get_channel(_channel_id||null): Promise.resolve(null)//doc.ref.id
        ]).then((values)=>{
          
          data = values[0]
          parent_chain = values[1]
          if(values[2]){channel = values[2][0]}
        })
      // second pipeLine
      if(!channel){
        _channel_id = channel_id || parent_chain[parent_chain.length-1].id
        await controller.get_channel(_channel_id).then((x)=>{channel= x[0]})
      }
      _appState.header_channel.value = channel
      return {
        data,
        parent_chain,
        channel
      }
    }

    let {data,parent_chain,channel} = await dataPipeLine(route_params.contentId,null)


    _appState.page_fetch_holder.value["space_list"] = data  //( {"space_list": data } )

    _appState.parent_chain.value = parent_chain;
    _appState.temptext1.value = channel.title
    
    return {
      _saving,
      content: ref(data[0].content || data[0].title ),
      parent_chain: ref(parent_chain),
      fn_save,
      fn_delete,
      popDialogue_display,
      schema_space_list: schema.space_list,
      
    };
  },
  props: {
    headerText: Object,
    headerText2: Object,
  },
};
</script>

