<template>
  <div class="e-vue-notecard-content" >
    <div class="e-vue-notecard-content-panel">
      <a class="e-vue-notecard-btn" v-if="userLoggedIn__sameChannel" href="?edit=true">
        Edit
      </a>
    </div>
    <div class="e-editor-head" style=""> 
      <div class="e-editor-head-inner">
        {{content}}
      </div>
    </div>
    <div id="aaeditor">

    </div>
  </div>
</template>

<script>

import EditorJS from "@editorjs/editorjs";
import Header from "@editorjs/header";
import List from "@editorjs/list";
import ImageTool from "@editorjs/image";
import LinkTool from "@editorjs/link";``
import Embed from "@editorjs/embed";
import Delimiter from "@editorjs/delimiter";
import CodeTool from "@editorjs/code";

import { computed, ref, onMounted } from 'vue'
//import { useRoute } from 'vue-router'
import router from '@/router/index'
import Controller from '../modules/controller.js'
import appState from '../modules/appState.js'



export default {
  name: 'notecard-content',
  async setup(props,context) {
    let editor = {}
    onMounted( () => {
      
      editor = new EditorJS({
        holderId: "aaeditor",
        autofocus: false,
        readOnly: true,
        tools: {
          code:{
            class: CodeTool,
          },
          header: {
            class: Header,
            inlineToolbar: ["link"],
            config: {
              placeholder: "Header",
            },
            shortcut: "CMD+SHIFT+H",
          },
          image:{
            class: ImageTool,
            config: {
              uploader: {
                uploadByFile(file){
                  console.log("uploadByFile")
                  let uploading_img = new Promise( (resolve, reject) =>{
                      if(!file){
                          resolve({
                            "success" : 0
                          })
                      }
                      let storageRef = firebase.storage().ref('user_uploads/'+file.name)
                      let task = storageRef.put(file)
                      let snapshot_holder = null
                      task.on("state_changed",
                          function progress(snapshot) {
                              snapshot_holder = snapshot
                              console.log("progress img")
                          },
                          function error(err) {
                              console.log("err")
                              failure('failed img')
                          },
                          function complete(x) {
                              console.log("complete img")
                              snapshot_holder.ref.getDownloadURL().then(function (downloadURL) {
                                  resolve({
                                    "success" : 1,
                                    "file": {
                                        "url" : downloadURL,
                                    }
                                  })
                              });
                          }
                      )
                  })
                  return uploading_img

                }
              },
            }
          },
          list: {
            class: List,
            inlineToolbar: true,
            shortcut: "CMD+SHIFT+L",
          },

          linkTool: LinkTool,

          embed: Embed,
        },
        data: {
          blocks: data[0]["note_data"]
        },
      });
    })

    const _appState = appState()
    const { currentRoute } = router
    const route_params = currentRoute._value.params
    let controller= Controller()
    const user = controller.getUser()

    // let data = await controller.get_note(route_params.contentId)
    // let parent_chain = await controller.get_note_parentChain(route_params.contentId)    
    
    let dataPipeLine = async function(pid, channel_id){ // get_noteArray, get_note_parentChain, get_channel
      let data = null
      let parent_chain = null
      let channel = null

      let _channel_id = channel_id
      // first pipeLine
      await Promise  
        .all([
          controller.get_note(pid),
          controller.get_note_parentChain(pid),
          _channel_id ? controller.get_channel(_channel_id||null): Promise.resolve(null)//doc.ref.id
        ]).then((values)=>{
          
          data = values[0]
          parent_chain = values[1]
          if(values[2]){channel = values[2][0]}
        })
      // second pipeLine
      if(!channel){
        _channel_id = channel_id || parent_chain[parent_chain.length-1].id
        await controller.get_channel(_channel_id).then((x)=>{channel= x[0]})
      }
      console.log("")
      _appState.metaTitle.value = `${data[0].title} | AtticStone`
      _appState.metaDescription.value = `${data[0].seo_description || data[0].title} ---> AtticStone`
      return {
        data,
        parent_chain,
        channel
      }
    }

    let {data,parent_chain,channel} = await dataPipeLine(route_params.contentId,null)


    
    _appState.parent_chain.value = parent_chain
    _appState.temptext1.value = channel.title
    _appState.header_channel.value = channel

    const userLoggedIn__sameChannel = computed({
      get: () => { 
        return (user.value.displayName && !(user.value.displayName===null)) && (user.value.uid == _appState.header_channel.value.uid)
      }
    })
    return {
      userLoggedIn__sameChannel,
      content: ref(data[0].content || data[0].title ),
      parent_chain: ref(parent_chain),
    }
  },
  props: {
    headerText: Object,
    headerText2: Object
  }
}
</script>

