<script>
import {ref} from "vue"
import NoteCard from './NoteCard.vue'
import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'
import Controller from '../modules/controller.js'
import appState from '../modules/appState.js'
import schema from '@/modules/schema.js'
import router from '@/router/index'
import PopupHolderV2 from '@/components/PopupHolder_v2.vue'
import FCPopupHolder from '@/components/FC_popupHolder.vue'




function shuffle(array) {
  var currentIndex = array.length, temporaryValue, randomIndex;
  while (0 !== currentIndex) {
    
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }
  return array;
}

export default {
  name: 'homeWrapper',
  components: {
    NoteCard,
    PopupHolderV2,
    FCPopupHolder,
    Header,
    Footer
  },
  async setup(props, ctx) {
        const { currentRoute } = router
        const route_params = currentRoute._value.params
        
        const _appState = appState()
        const _dim1 = props.dim1 || 5
        const _dim2 = props.dim2 || 5
        let controller= Controller()

        let member_list = await controller.get_memberGMeta_list_array()

        member_list = member_list.reduce(function(obj,item){
          obj[item.uid] = item; 
          return obj;
        }, {});

        let data = await controller.get_channel_list(route_params.pid, route_params.owner_id)
        return {
            member_list,
            ChannelList_populated: ref(data),
            schema_space_list: schema.space_list,
            schema_channel_list: schema.channel_list,
            EVWrapperV2_post_process: controller.EVWrapperV2_post_process
        }
  },
  props: {
    msg: String,
    dim1: String,
    dim2: String,
  }
}
</script>

