import Controller from "@/modules/controller.js";
import appState from "@/modules/appState.js";



let FC__EVWrapperV2_default_submit = (formData, busy, root, currentRoute, props, action)=>{ // using FC because we need the variable from the closure where the function is to be invoked. But we can not get it.  
    return async () => {
        if (busy.value) {
            return;
        }
        if (!firebase.auth().currentUser) {
            return;
        }
        busy.value = true;
    
        formData.value = Array.from(
            (formData.value = root.value.querySelectorAll(".e-v-input"))
        ).map((x) => {
            return x.value;
        });
        //let slotArray = context.slots.default();
        let controller = Controller()
        const _appState = appState();

        let dataFirestore = {};
        let data = Array.from(root._rawValue.querySelectorAll(".e-v-input"))
            .map((el) => {
                let rtn = {};
    
                if (el.tagName == "INPUT" && el.type == "text") {
                    rtn[el.dataset.key] = el.value;
                } else if (el.tagName == "TEXTAREA" && el.type == "textarea") {
                    rtn[el.dataset.key] = el.value;
                } else if (el.tagName == "INPUT" && el.type == "file") {
                    rtn[el.dataset.key] = e_refs.value.value.ewrapper.e_refs.value[el.dataset.key].fc_uploading_img;
                } else if (el.tagName == "SELECT") {
                    rtn[el.dataset.key] = el.value;
                }
    
                return rtn;
            })
            .forEach((x) => {
    
    
                let key = Object.keys(x)[0];
                let value = Object.values(x)[0];
                if (key == "type") {
                    dataFirestore["type"] = value || "-1";
                } else if (key == "content") {
                    dataFirestore["content"] = value || "";
                } else {
                    dataFirestore[key] = value;
                }
            });
        for (let x in dataFirestore) {
    
            let el = dataFirestore[x]
            if (el.prototype) {
                if (el.prototype.selfType == "promise") {
                    dataFirestore[x] = await el()
                }
            }
            if (dataFirestore[x] === null) {
                delete dataFirestore[x]
            }
        }
    
    
        dataFirestore["id"] = controller.uuidv4();
        dataFirestore["uid"] = firebase.auth().currentUser.uid;
        dataFirestore["createTime"] = firebase.firestore.Timestamp.now();
        dataFirestore["parent_id"] = currentRoute._value.params.pid || "-1";
        dataFirestore["channel_id"] = currentRoute._value.params.channel_id || "-1";
        dataFirestore["status"] = "onWeb"
        // validation
        let validation_ref = props.schema.form.input.reduce((acc, el) => { acc[el.inputkey] = el; return acc; }, {})
    
    
        for (let x in dataFirestore) {
    
            if (validation_ref[x] && validation_ref[x].validation) {
                let inc_right_el = root._rawValue.querySelector(`.e-v-input[data-key='${x}']`).parentNode.querySelector(".e-v-input-inc_right")
                let v_rtn
                v_rtn = validation_ref[x].validation(dataFirestore[x])
                if (v_rtn == "v_rtn_false") {
                    busy.value = false;
                    alert("validation failed")
                    inc_right_el.classList.add("invalid")
                    root._rawValue.querySelector(".e-v-formwrapper-btn").classList.add("invalid")
    
                    return
                } else {
                    inc_right_el.classList.remove("invalid")
                    root._rawValue.querySelector(".e-v-formwrapper-btn").classList.remove("invalid")
                }
                dataFirestore[x] = v_rtn
            }
        }
        if (props.EVWrapperV2_post_process) {
            dataFirestore = props.EVWrapperV2_post_process(dataFirestore,_appState)
        }
        if(action=='add'){

        
            firestore
                .collection(props.schema.form.collectionStr)
                .add(dataFirestore)
                .then(() => {
                    busy.value = false;
                })
                .then(() => {
                    location.reload();
                });
        }else if(action=='edit'){
            
            
            let d_key = _appState.page_fetch_holder.value[props.modelObj_key][0].d_key
            console.log("d_key")
            console.log(d_key)

            delete dataFirestore["id"] 
            delete dataFirestore["uid"] 
            delete dataFirestore["createTime"] 
            delete dataFirestore["parent_id"] 
            delete dataFirestore["channel_id"] 
            delete dataFirestore["status"] 

            await firestore
                .collection(props.schema.form.collectionStr)
                .doc(d_key)
                .update(dataFirestore)
                .then(() => {
                    busy.value = false;
                })
                .then(()=>{
                  location.reload();
                })
            
        }
        
    };
}

export { FC__EVWrapperV2_default_submit }