
    
<template>
  
  <Header :mainText="_appState.temptext1" :channel="_appState.header_channel"></Header>
  <div class="e-vue-cardcontentwrapper" :style="{'grid-template-columns': 'repeat('+_dim1+', 1fr)','grid-template-rows': 'repeat('+_dim2+', 1fr)'}">
      
