</template>



<script>
import {ref,reactive,onMounted} from "vue"
import NoteCardContent from './NoteCard_content.vue'
import NoteCardContentEdit from './NoteCard_edit.vue'
import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'
import Controller from '../modules/controller.js'
import appState from '@/modules/appState.js'
import schema from '@/modules/schema.js'
import router from '@/router/index'
import TestComponent from '@/components/testComponent.vue'
import FCPopupHolder from '@/components/FC_popupHolder.vue'


export default {
  name: 'notecard',
  components: {
    NoteCardContent,
    Header,
    NoteCardContentEdit,
    TestComponent,
    FCPopupHolder
  },
  // props:{
  //   approute: Object
  // },
  async setup(props, ctx) {
        //console.log("approute")
        //console.log(props.approute)

        const { currentRoute } = router
        const router_query = currentRoute.value.query
        
        //console.log("currentRoute")

        let headerText = ref("sssssff")
        let headerText2 = reactive({str:"sssssff"})

        const _appState = appState()
        let controller= Controller()
        _appState.temptext1.value = ""
        
        onMounted(async () => {
          
          console.log("onMounted1")
        })
        return {
           _appState:_appState,  
           headerText:headerText,
           headerText2:headerText2,
           router_query:router_query,
           schema_space_list: schema.space_list,
           
           EVWrapperV2_post_process:(data)=>{
              console.log("EVWrapperV2_post_process")
              let channel = _appState.raw_channel.value[0]
              let parent_chain = _appState.parent_chain

              if(!channel.fkey){
                alert("no channel")
              }
              data["payload"] = data["payload"]? data["payload"]: {}
              data["payload"]["cref"] = channel.fkey//
              if(parent_chain.length){
                data["payload"]["sref"] = parent_chain[0].d_key
              }
              data["stuctureDepth"]=_appState.parent_chain.value.length.toString()
              
              return data
            }
        }
  },
  props: {

  }
}
</script>

