<template>
  
  <div class="e-v-input-root" ref="root">
      <label class="e-v-input-label text-stretch-after">{{htmlLabel}}</label>
      <input type="file" :class="[{'e-v-input':true}, {'e-v-input-image':true},`e-v-input-${inputkey}`]" :data-key="inputkey" id="input_add_product_img" accept="image/png, image/jpeg">
      <div class="e-v-input-inc_right"></div>
  </div>
  <div class="e-v-input-imgwrapper">
    <img class="e-v-input-img_preview"  :src="_default" alt="" >
  </div>
  
  
</template>

<script>
import {ref} from "vue"
import appState from '@/modules/appState.js'


export default {
  name: 'e-v-input-img',
  props: {
    inputkey: String,
    inputlabel: String,
    label: String,
    default: String
  },
  async setup(props,context) {

      const _default = ref(props.default)
      let img_file = null
      let fc_uploading_img = function(){
        return new Promise( (resolve, reject) =>{
          img_file = root._rawValue.querySelector("input").files[0]
          if(!img_file){
            resolve(null)
          }
          let refPath = 'uploads/'+img_file.name
          let storageRef = firebase.storage().ref(refPath)
          
          let task = storageRef.put(img_file)
          let snapshot_holder = null
          task.on("state_changed",
              function progress(snapshot) {
                  snapshot_holder = snapshot
                  console.log("progress img")
              },
              function error(err) {
                  console.log("err")
                  failure('failed img')
              },
              function complete(x) {
                  console.log("complete img")
                  snapshot_holder.ref.getDownloadURL().then(function (downloadURL) {
                      _default.value = downloadURL
                      resolve(downloadURL)//success_add(downloadURL,downloadURL_pdf)
                  });
              }
          )

        })
        .then((value)=>{
          console.log("resolved")
          return value 
        })
      }
      fc_uploading_img.prototype.selfType = "promise"
      
      // =============================

      const root = ref(null)
      const _appState = appState()
      const strVal = ref("")
      const htmlLabel = ref(props.inputlabel??props.inputkey)
    
      return {
          root,
          strVal,
          htmlLabel,
          //e_click,
          fc_uploading_img,
          _default
          //_click
      }
  }
}
</script>

