        <div class="e-vue-memberBody-infowrapper-head-i e-ii-2">
          <div class="e-vue-memberBody-infowrapper-head-i-name">
            {{member_list.name}}
          </div> 
          <div class="e-vue-memberBody-infowrapper-head-i-description">
