          </div> 
        </div>
        
      </div>
      <div id="t-test">

      </div>

      <div class="e-vue-memberBody-infowrapper-content">
        <div style="
            width: max-content;
            font-size: 1.2em;
            margin-bottom: 5px;
        ">
          
        </div>
        <div class="e-vue-memberBody-infowrapper-content-inner">
          <div class="e-vue-memberBody-infowrapper-channels">
            
            <a :class="[{'e-channel-i':true}, {'active': x._active?true:false }]" v-for="(x, ii) in ccchannel_list" :key="x" :data-uiindex="ii"  @click="e_click_channel_i(x)">
              <div class="e-channel-i-inner">
                {{x.title}}
              </div>
              <transition name="fade" 
                @before-leave="fn_beforeLeave"
                @leave="fn_leave"
                @after-leave="fn_afterLeave"
                :duration="4000"
              >
                <div v-if="(innerWidth < 768) && x._active" class="e-channel-i-inner-holder" >
                  <div  v-for="child in x.nextChild"  :key="child">
                    <div v-if="x.nextChild">
                      {{ child.title }}
                    </div>
                  </div>
