              </transition>
            </a>
          </div>
          <transition name="fade" 
                @before-leave="fn_beforeLeave"
                @leave="fn_leave"
                @after-leave="fn_afterLeave"
                :duration="4000"
              >
          <div class="e-vue-memberBody-infowrapper-space" v-if="innerWidth >= 768">
            <div class="e-vue-memberBody-infowrapper-spaceinner">
              <div class="e-space-i" v-for="x in data.list" :key="x"  >
                <router-link :to="channel_space_href(x)">
                  {{ x.title }}
                </router-link>
              </div>
            </div>
          </div>
