<script>
import { ref, computed, onMounted, reactive } from "vue";
import NoteCard from "./NoteCard.vue";
import Header from "@/components/Header.vue";
import Footer from '@/components/Footer.vue'
import Controller from "../modules/controller.js";
import appState from "../modules/appState.js";
import schema from '@/modules/schema.js'
import router from "@/router/index";
import PopupHolderV2 from "@/components/PopupHolder_v2.vue";
import FCPopupHolder from "@/components/FC_popupHolder.vue";

import EVInput from "@/components/inputs/e-v-input";
import EVInputSelect from "@/components/inputs/e-v-input-select";
import EVWrapperV2 from "@/components/inputs/e-v-wrapper-v2";
import { channel_space_href } from '@/modules/process_href.js'

//import func from '../../vue-temp/vue-editor-bridge'

function shuffle(array) {
  var currentIndex = array.length,
    temporaryValue,
    randomIndex;
  while (0 !== currentIndex) {
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }
  return array;
}

export default {
  name: "homeWrapper",
  components: {
    NoteCard,
    PopupHolderV2,
    FCPopupHolder,
    Header,
    EVInput,
    EVInputSelect,
    EVWrapperV2,
    Footer
  },
  async setup(props, ctx) {
 
    let controller = Controller();
    const _appState = appState();

    const { currentRoute } = router;
    const route_params = currentRoute._value.params;

    
    const ewrapper = ref(null)
    const e_refs = ref({});
    const _dim1 = props.dim1 || 5;
    const _dim2 = props.dim2 || 5;
    const data = reactive({
      list: []
    })
    const user = controller.getUser()
    
    
    
    let member_list_old = await controller.get_memberGMeta_list_array() // Just a padding for time. Hack fix for time.
    let member_list = await controller.get_memberGMeta_by_id(route_params.id)
    let channel_list = await controller.get_channel_uid(member_list[0].uid)// => should not be id, should be uid      
    channel_list[0]._active = true
    channel_list[0].nextChild = await controller.get_noteArray("-1", channel_list[0].id)
    data.list = channel_list[0].nextChild


    let channel_active = channel_list[0]
    let ccchannel_list = reactive(channel_list)
    

    let e_click_channel_i = function(){
        var busy = false;
        return async function(x) {
          console.log("e_click_channel_i")
          if(busy){
            return
          }
          busy = true
          setTimeout(function(){
            busy = false
          },500)

          channel_active._active = false

          if(!x.nextChild){
            x.nextChild = await controller.get_noteArray("-1", x.id)  
          }
          data.list = x.nextChild
          console.log(data)
          
          channel_active = x
          x._active = true
        }
    }()
    
    

    let fc_memberSchema = (member_obj)=>{
      
      let rtn = schema.memberGMeta_list
      if(!member_obj){
        rtn["flag"] = "new"
        return rtn
      }
      let member_obj_key = Object.keys(member_obj)
      for (let x in rtn.form.input){
        let el = rtn.form.input[x]
        let ii = member_obj_key.indexOf(el.inputkey)
        if(ii >= 0){
          el['default'] = member_obj[member_obj_key[ii]]
        }
      }
      return rtn
    }


    let memberSchema = fc_memberSchema(member_list[0])

    let e_click = async () => {
      console.log("e_click")
      
      await fc_submit_contextRef();
      console.log("saved")
    };
    let fc_submit_contextRef = () => {};
    let fc_submit = function (context2_root) {
      let fc_rtn = async function () {
        
        let dataFirestore = {};
        let data = Array.from(context2_root._rawValue.querySelectorAll(".e-v-input"))
          .map((el) => {
            let rtn = {};
            if (el.tagName == "INPUT" && el.type=="text") {
              rtn[el.dataset.key] = el.value;
            } else if(el.tagName == "TEXTAREA" && el.type=="textarea"){
              rtn[el.dataset.key] = el.value;
            } else if(el.tagName == "INPUT" && el.type=="file"){
              rtn[el.dataset.key] = e_refs.value.value.ewrapper.e_refs.value[el.dataset.key].fc_uploading_img;
            } else if (el.tagName == "SELECT") {
              rtn[el.dataset.key] = el.value;
            }
            return rtn;
          })
          .forEach((x) => {
            let key = Object.keys(x)[0];
            let value = Object.values(x)[0];
            if (key == "type") {
              dataFirestore["type"] = value || "-1";
            } else if (key == "content") {
              dataFirestore["content"] = value || "";
            } else {
              dataFirestore[key] = value;
            }
          });
        for(let x in dataFirestore){
          
          let el = dataFirestore[x]
          if(el.prototype){
            if(el.prototype.selfType=="promise"){
              dataFirestore[x] = await el()
            }
          }
          if(dataFirestore[x]===null){
            delete dataFirestore[x]
          }
        }
        dataFirestore["id"] = Controller().uuidv4();
        dataFirestore["uid"] = firebase.auth().currentUser.uid;
        dataFirestore["createTime"] = firebase.firestore.Timestamp.now();
        dataFirestore["parent_id"] = currentRoute._value.params.pid || "-1";
        dataFirestore["channel_id"] = currentRoute._value.params.channel_id || "-1";
        if(memberSchema.flag=="new"){
          await firestore
            .collection(memberSchema.form.collectionStr)
            .doc(firebase.auth().currentUser.uid)
            .set(dataFirestore)
            .then(()=>{
              memberSchema.flag="tobeupdated"
            })
        }else if(memberSchema.flag=="tobeupdated"){
          await firestore
            .collection(memberSchema.form.collectionStr)
            .doc(firebase.auth().currentUser.uid)
            .update(dataFirestore)
        }
      };
      fc_submit_contextRef = fc_rtn;
      return fc_rtn;
    };
    
    return {
      e_appState: _appState,
      member_list: member_list[0],
      e_refs,
      ewrapper,
      fc_submit: fc_submit,
      memberSchema: memberSchema,
      channel_list, 
      //aachannel_list,
      schema_space_list: schema.space_list,
      schema_channel_list: schema.channel_list,
      // user2,
      e_click_channel_i,
      ccchannel_list,
      data,
      innerWidth: window.innerWidth,
      channel_space_href,
      fn_beforeLeave: (el)=>{
        let height = window.getComputedStyle(el).getPropertyValue("height")
        el.style.height = height
        console.log(height)
        console.log('dddddfffgee')
      },
      fn_leave: (el)=>{
        console.log('fn_leave')
        setTimeout(function(){
          el.style.height = "0px"
          el.style.marginTop = "0px"
        },100)
      },
      fn_afterLeave: (el)=>{
        el.style.height = ""
        el.style.marginTop = ""
      }
    };
  },
  mounted() {
    this.e_refs.value = this.$refs
  },
  props: {
    msg: String,
    dim1: String,
    dim2: String,
  },
};
</script>

