<template>
  <Header :mainText="mainText" :channel="channel" ></Header>
  <div class="e-vue-cardwrapper" :style="{'grid-template-columns': 'repeat('+_dim1+', 1fr)','grid-template-rows': 'repeat('+_dim2+', 1fr)'}">
    <div v-for="x in NoteList_populated" :key="x">
      <Suspense>
        <NoteCard :data="x" :pdim1="x.content_dim_x" :pdim2="x.content_dim_y" :id="x.id">
          
        </NoteCard>
      </Suspense>
    </div>
  </div>
  <div>
    <FCPopupHolder  :instanceKey="'schema_space_list'" 
                    :popupSchema="schema_space_list" 
                    :EVWrapperV2_post_process="EVWrapperV2_post_process"
                    :action="'add'"
                    />
  </div>
  <Footer></Footer>
</template>
<script>
import {ref, onMounted, onUpdated} from "vue"
import NoteCard from './NoteCard.vue'
import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'
import Controller from '../modules/controller.js'
import appState from '../modules/appState.js'
import router from '@/router/index'
// import PopupHolder from '@/components/PopupHolder.vue'
import schema from '@/modules/schema.js'
import FCPopupHolder from '@/components/FC_popupHolder.vue'

function shuffle(array) {
  var currentIndex = array.length, temporaryValue, randomIndex;
  // While there remain elements to shuffle...
  while (0 !== currentIndex) {
    
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }
  return array;
}

export default {
  name: 'notecard-wrapper',
  components: {
    NoteCard,
    FCPopupHolder,
    Header,
    Footer
  },
  async setup(props, ctx) {
        onMounted(() => {
          console.log("onMounted")
          document.querySelector(".e-header").classList.add("mode2")
        })
        const { currentRoute } = router
        const route_params = currentRoute._value.params
        
        const _appState = appState()
        const _dim1 = props.dim1 || 5
        const _dim2 = props.dim2 || 5
        let controller= Controller()

        // let data = null
        // let parent_chain = null
        // let channel = null

        let _channel_id = route_params.channel_id || null//parent_chain[parent_chain.length-1].id

        let dataPipeLine = async function(pid, channel_id){ // get_noteArray, get_note_parentChain, get_channel
          let data = null
          let parent_chain = null
          let channel = null

          let _channel_id = channel_id
          // first pipeLine
          await Promise  
            .all([
              controller.get_noteArray(pid, channel_id),
              controller.get_note_parentChain(pid),
              _channel_id ? controller.get_channel(_channel_id||null): Promise.resolve(null)//doc.ref.id
            ]).then((values)=>{
              data = values[0]
              parent_chain = values[1]
              if(values[2]){channel = values[2][0]}
            })
          // second pipeLine
          if(!channel){
            _channel_id = channel_id || parent_chain[parent_chain.length-1].id
            await controller.get_channel(_channel_id).then((x)=>{channel= x[0]})
          }
          return {
            data,
            parent_chain,
            channel
          }
        }

        let {data,parent_chain,channel} = await dataPipeLine(route_params.pid,route_params.channel_id)
        
        
        console.log("remain")
        _appState.due_channel.value = channel
        _appState.parent_chain.value = parent_chain
        
        
        data = Array((_dim1*_dim2) - data.length)
          .fill({})
          .map((x,ii)=>{return {id:ii}})
          .concat(data)
          .map((el)=>{ 
            if(el.layout_type){
              let [x, y] = el.layout_type.split("_")
              el.content_dim_x = x
              el.content_dim_y = y
            } 
            return el
          })
        console.log("data1") 
        data = shuffle(data)
      
        
        
        
        return {
            mainText: channel?channel.title:"null",
            channel:channel,
            _dim1:ref(_dim1),
            _dim2:ref(_dim2),
            NoteList_populated: ref(data),
            schema_space_list: schema.space_list,
            EVWrapperV2_post_process:(data)=>{
              console.log("EVWrapperV2_post_process")
              
              if(!channel.fkey){
                alert("no channel")
              }
              data["payload"] = data["payload"]? data["payload"]: {}
              data["payload"]["cref"] = channel.fkey//
              if(parent_chain.length){
                data["payload"]["sref"] = parent_chain[0].d_key
              }
              data["stuctureDepth"]=_appState.parent_chain.value.length.toString()
              
              return data
            }
        }
  },
  props: {
    msg: String,
    dim1: String,
    dim2: String,
  }
}
</script>

