<template>
  <div class="e-v-formwrapper" ref="root">
    <div v-for="x in formInput" :key="x" class="e-v-formwrapper-i">
        <transition name="fade" 
          @before-leave="fn_beforeLeave"
          @leave="fn_leave"
          @after-leave="fn_afterLeave"
          :duration="4000"
        >
          <EVInput v-if="x.type=='textString'" 
            :ref="x.inputkey" :inputkey="x.inputkey" 
            :inputlabel="x.inputlabel" 
            :default="x.default" 
            :onChange="x.onChange" >
          </EVInput>
        </transition>

        <transition name="fade" 
          @before-leave="fn_beforeLeave"
          @leave="fn_leave"
          @after-leave="fn_afterLeave"
          :duration="4000"
        >
          <EVInputSelect v-if="x.type=='select'" 
            :ref="x.inputkey" :inputkey="x.inputkey" 
            :inputOptions="x.inputOptions" 
            :defaultOption="false" 
            :default="x.default" 
            :onChange="x.onChange" 
            >
          </EVInputSelect>
        </transition>

        <transition name="fade" 
          @before-leave="fn_beforeLeave"
          @leave="fn_leave"
          @after-leave="fn_afterLeave"
          :duration="4000"
        >
          <EVInputImage v-if="x.type=='image'" 
            :ref="x.inputkey" :inputkey="x.inputkey" 
            :inputlabel="x.inputlabel" 
            :default="x.default"
            :onChange="x.onChange"
          >
          </EVInputImage>
        </transition>

        <transition name="fade" 
          @before-leave="fn_beforeLeave"
          @leave="fn_leave"
          @after-leave="fn_afterLeave"
          :duration="4000"
        >
          <EVInputTextarea v-if="x.type=='textarea'" 
            :ref="x.inputkey" :inputkey="x.inputkey" 
            :inputlabel="x.inputlabel"  
            :default="x.default"
            :onChange="x.onChange"
          >
          </EVInputTextarea>
        </transition>
        
        <transition name="fade" 
          @before-leave="fn_beforeLeave"
          @leave="fn_leave"
          @after-leave="fn_afterLeave"
          :duration="4000"
        >
          <EVIUIDropdwon v-if="x.type=='ui_control_drop'" 
            :inputlabel="x.inputlabel"  
            :default="x.default"
            :onClick="x.onClick"
          >
          </EVIUIDropdwon>
        </transition>
    </div>
    <button class="e-v-formwrapper-btn" v-if="!options.no_btn" @click="e_submit">
      confirm
    </button>
  </div>
</template>

<script>
import { ref, computed, onMounted } from "vue";
import appState from "@/modules/appState.js";
import Controller from "@/modules/controller.js";
import router from "@/router/index";

import EVInputImage from '@/components/inputs/e-v-input-img'
import EVInput from '@/components/inputs/e-v-input'
import EVInputSelect from '@/components/inputs/e-v-input-select'
import EVInputTextarea from '@/components/inputs/e-v-input-textarea'

import EVIUIDropdwon from '@/components/inputs/e-v-ui-dropdown'

import {FC__EVWrapperV2_default_submit} from '@/components/js_modules/e-v-inputs.js'



export default {
  name: "e-v-wrapper-v2",
  props: {
    action: String,
    modelObj_key: String,
    collectionStr: String,
    key: String,
    labekl: String,
    fc_submit: Object,
    schema: Object,
    EVWrapperV2_post_process: Object,
    options:  {
      type: Object,
      default: {no_btn:false}
    }
  },
  components: {
    EVInput,
    EVInputImage,
    EVInputTextarea,
    EVInputSelect,

    EVIUIDropdwon
  },
  async setup(props, context) {
    console.log("e-v-wrapper-v2")
    const { currentRoute } = router;

    const root = ref(null);
    const formData = ref({});
    const _appState = appState();
    const e_refs = ref({});
    let busy = {value:false};
    let e_submit;
    if (!props.fc_submit) {
      e_submit = () => {} // not logical to define here for the reason of maintainability
      e_submit = FC__EVWrapperV2_default_submit(formData, busy, root, currentRoute, props, props.action)
      
    }else{
        e_submit = props.fc_submit(root)
    }

    if(props.action=="add"){
      console.log("init add")
      for(let x in props.schema._raw.form.input){
          let _raw_obj = props.schema._raw.form.input[x]
          let _obj = props.schema.form.input[x]
          _obj.default = _raw_obj.default
          if(_raw_obj.defaultOption){
            _obj.default = _raw_obj.defaultOption
          }
      }
    }
    
    return {
      fn_beforeLeave: (el)=>{
        let height = window.getComputedStyle(el).getPropertyValue("height")
        el.style.height = height
        console.log(height)
        console.log('dddddfffgee')
      },
      fn_leave: (el)=>{
        console.log('fn_leave')
        setTimeout(function(){
          el.style.height = "0px"
          el.style.marginTop = "0px"
        },100)
      },
      fn_afterLeave: (el)=>{
        el.style.height = ""
        el.style.marginTop = ""
      },
      formInput: computed(()=>{
        let rtn = props.schema.form.input
              .filter( (x)=>{ return x.toggle==undefined || x.toggle==true} )

        if(props.modelObj_key){
          console.log('set default value')
          console.log(_appState.page_fetch_holder.value[props.modelObj_key][0])
          let _model = _appState.page_fetch_holder.value[props.modelObj_key][0]
          rtn.forEach((x)=>{
              let inputkey = x.inputkey
              if(inputkey){
                x.default = _model[inputkey]
              }
          })
        }
        return rtn
      }),
      root,
      e_submit,
      context,
      e_refs
    };
  },
  mounted() {
    
    this.e_refs.value = this.$refs
  }
};
</script>

