    color: white;
    font-size: 1.1rem;
    letter-spacing: 0.05em;
    font-weight: bolder;
    position: relative;
  }
  .e-v-formwrapper-btn:hover{
    background: #40372f;
  }
  
  /* >>> is deprecated */
  .e-v-formwrapper:deep(.e-v-input-inc_right) { 
    display: inline;
    position: relative;
    left: 6px;
    top: 1px;
  }
  .e-v-formwrapper:deep(.e-v-input-inc_right.invalid::before) {
    color: brown;
    position: absolute;
    left: 0;
    content: "";
    font-size: 1.1em;
  }
  .e-v-formwrapper-btn.invalid::before{
    content: "data invalid";
    color: brown;
    display: inline-block;
    position: absolute;
    bottom: 8px;
    left: calc(100% + 10px);
    font-size: 10px;
    width: max-content;

  }
  .fade-enter-active, .fade-leave-active {
    transition: all .5s !important;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>
