<template>
  <transition name="fade"  :duration="3000">
    <div class="e-header" v-show="pageYOffset<20">
      <div class="e-header-inner">
        <router-link v-if="channel" :to="`/channel/${channel.id}`">{{channel.title}}</router-link>
        <router-link v-else to="/">{{mainText}}</router-link>
        <div class="e-header-dirwrapper">
          <div class="e-header-dir" v-if="appState.parent_chain.value" v-for="x in filter2space(proxy_reverse(appState.parent_chain.value))" :key='x'>
            <div class="e-header-dir-sp">>> </div> 
            <router-link 
              :to=" channel_space_href(x)"
            >
              {{x.title}}
            </router-link>
          </div>
        </div>
      </div>
      <div class="e-header-inc"></div>
    </div>
  </transition>  
</template>

<script>
import { ref, onBeforeMount, onBeforeUnmount } from 'vue'
import appState from '@/modules/appState.js'
import router from '@/router/index'
import { channel_space_href } from '@/modules/process_href.js'

export default {
  name: 'HelloWorld',
  props: {
    msg: String,
    mainText: String, // should be replaced gradually
    channel:Object
  },
  setup(props,context) {
    const _appState = appState()
    const { currentRoute } = router
    const route_params = currentRoute._value.params
    const pageYOffset = ref(window.pageYOffset) 
    const _handleScroll = ()=>{
      pageYOffset.value = window.pageYOffset
      //console.log("_handleScroll")
    }
    onBeforeMount(()=>{
      window.addEventListener('scroll', _handleScroll);
    })
    onBeforeUnmount(()=>{
      window.removeEventListener('scroll', _handleScroll);
    })
    console.log("header setup")
    return {
      appState: _appState,
      proxy_reverse: _appState.utils.proxy_reverse,
      filter2space: _appState.utils.filter2space,
      pageYOffset,
      mainText: props.channel?props.channel.title:null || props.mainText || "channelName",
      channel: props.channel,
      channel_space_href
    }
  },

}
</script>

