<template>
    <div class="e-v-input-root" ref="root" @click="onClick">
        <label class="e-v-input-label e-ui-label text-stretch-after" >{{htmlLabel}} </label>
    </div>
</template>

<script>
import {ref} from "vue"
import appState from '@/modules/appState.js'


export default {
  name: 'e-v-ui-dropdown',
  props: {
    inputlabel: String,
    default: String,
    onClick: Object,
  },
  async setup(props,context) {
      const root = ref(null)
      const _appState = appState()
      const strVal = ref(props.default?props.default:"")
      const htmlLabel = ref(props.inputlabel??props.inputkey)


      return {
          onClick:function() {
            var value = false;
            return function() {
              value = !value

              let context = {}
              context.value = value
              props.onClick(props, context)
              return;
            }
          }(),
          // onClick:(ev)=>{
          //   console.log("Click")
          //   let context = {}
          //   context.value = ev.target.value
          //   props.click(props, context)
          // },
          root,
          strVal,
          htmlLabel
      }
  }
}
</script>

