<template>
    <div class="e-v-input-root" ref="root" style="    flex-direction: column;align-items: baseline;">
        <label class="e-v-input-label text-stretch-after">{{htmlLabel}}</label>
        <textarea :class="[{'e-v-input':true},`e-v-input-${inputkey}`]" :data-key="inputkey" name="" id="" cols="30" rows="10" v-model="strVal">  
        </textarea>
        <div class="e-v-input-inc_right"></div>
    </div>
</template>

<script>
import {ref} from "vue"
import appState from '@/modules/appState.js'


export default {
  name: 'e-v-input',
  props: {
    inputkey: String,
    inputlabel: String,
    label: String,
    default: String
  },
  async setup(props,context) {
      const root = ref(null)
      const _appState = appState()
      const strVal = ref(props.default?props.default:"")
      const htmlLabel = ref(props.inputlabel??props.inputkey)

      return {
          root,
          strVal,
          htmlLabel
      }
  }
}
</script>

