<template>
  <teleport to="#tl-holder">
    
    <div id="tl-holder-inner" @click="notecard_add_close" v-if="popupInstance[instanceId]['toggle']">
      <div class="e-vue-notecard-popup">
        <div class="e-vue-notecard-popup-close" @click="notecard_add_close">
          <svg style="display: inline-block;width: 26px;height: 26px;">
            <use href="#svg__cross"></use>
          </svg>
        </div>
        <div class="e-vue-notecard-popup-title">
          {{popupInstance.schema_space_list.title_temp || popupSchema.title}}
        </div>
        <EVWrapperV2 
          ref="ewrapper" 
          :schema="popupSchema" 
          :EVWrapperV2_post_process="EVWrapperV2_post_process"
          :action="action"
          :modelObj_key="modelObj_key"
        >
        </EVWrapperV2>
      </div>
    </div>
  </teleport>
</template>

<script>
import {onUnmounted} from 'vue'
import EVInput from '@/components/inputs/e-v-input'
import EVInputSelect from '@/components/inputs/e-v-input-select'
import EVWrapperV2 from "@/components/inputs/e-v-wrapper-v2";


import Controller from '@/modules/controller.js'
import appState from '@/modules/appState.js'
import router from '@/router/index'

export default {
  name: 'PopupHolder_v2',
  props: {
    action: String,
    modelObj_key: String,
    instanceKey: String,
    popupSchema: Object,
    EVWrapperV2_post_process: Object
  },
  components: {
    EVWrapperV2,
    EVInput,
    EVInputSelect
  },
  setup(props,context){
      const _appState = appState()
      const controller= Controller()
      const instanceId = props.instanceKey || controller.randomId(6)
      const popupStateRegister = (ev)=>{
        _appState.popupInstance.value[instanceId] = {
          toggle: false
        }
      }
      popupStateRegister()

      const notecard_add_close = (ev)=>{
        let target = ev.target
        if(target.id == "tl-holder-inner"||
            target.classList.contains(".e-vue-notecard-popup-close")|| 
            target.closest(".e-vue-notecard-popup-close")
            ){
            _appState.tl_toggle.value = false
            document.querySelector("#tl-holder").classList.remove("on")
            return
        }else{
            return
        }
      }
      onUnmounted(()=>{
        delete _appState.popupInstance.value[instanceId]
      })
      console.log('popup_v2')
      return {
        action: props.action,
        modelObj_key:props.modelObj_key,
        popupSchema: props.popupSchema,
        EVWrapperV2_post_process: props.EVWrapperV2_post_process,
        instanceId:instanceId,
        popupInstance:_appState.popupInstance,
        notecard_add_close:notecard_add_close,
      }
  }
}
</script>


