<script>
import { reactive, onMounted, ref, computed } from 'vue'
import appState from './modules/appState.js'
// import router from './router/index'
import PopDialogue from "@/components/PopDialogue";
import { createWebHistory } from 'vue-router'
import Controller from "@/modules/controller.js";

export default {
  name: 'App',
  components:{
    PopDialogue
  },
  setup(props, ctx){
  
    const href = ref(location.href)
    //console.log('App')
    
    //const pathname = ref()
    let popDialogue_display = ref(new Proxy(
      {
        value:false,
        concent: false,
        text:`
          <div>
            <div style="margin-bottom: 6px">
            
            </div>
            <div style="padding: 0 10px; max-width: 650px;margin: auto;">
              <div style="text-align:left"></div>
            </div>
          </div>
        `,
        text2:`<div style="margin-left: auto;width:max-content;margin-right:29px;    margin-top: 18px;">

          <div style="display:inline-block">
            <input type="radio" type="radio" name="sdadsafs-concent" id="sdgsdlk-agree" style="margin-right: -2px;
    margin-left: 5px;" >
            <label for="sdgsdlk-agree" style="cursor:pointer">  </label>
          </div>
          <div style="display:inline-block">
            <input type="radio" type="radio" name="sdadsafs-concent" id="sdgsdlk-disagree" style="margin-right: -2px;
    margin-left: 5px;">
            <label for="sdgsdlk-disagree" style="cursor:pointer">  </label>
          </div>
        </div>`,
        text2_event:(ev, context)=>{
          if(ev.type=="change"){
            let _id = ev.target.id
            console.log(context.root)
            if(_id=="sdgsdlk-agree"){
              context.dialogue_ref.concent = true
               context.root._rawValue.querySelector(".e-pop_dialogue-text3 > *").style.display = "block"
            }
            if(_id=="sdgsdlk-disagree"){
              context.dialogue_ref.concent = false
              context.root._rawValue.querySelector(".e-pop_dialogue-text3 > *").style.display = "none"
            }
          }
        },
        text3:`
          <div class="e-pop_exitBTN" style="display: none">
            /
          </div>
        `,
        //text3Toggle:false,
        text3_event:(ev, callback_context)=>{
          firebaseGSignin()
          callback_context.fn_exit()
        },
        before_exit: (context)=>{
          
        }
      },
      {
        set: (obj, key, value)=>{
          if(key=="value"){
            if(obj[key] == true && value=="false2"){
              obj[key] = false
              //res()
              return true
            }else{
              obj[key] = value;
              //res()
              return true
            }
          }
          //res()
          obj[key] = value
          return true
          // return new Promise((res, rej) => {
              
          // })
        }
      }
    ))
    let controller = Controller();

    const routeHistory = createWebHistory()
    const user = controller.getUser()
    

    const droptoggle1 = ref(false)
    const dropwrapper_on = ref(false)
    const _appState = appState()
    document._appState = _appState
    
    //const { currentRoute } = router
    //const route_params = currentRoute._value.params

    console.log("app route")
    
    onMounted(()=>{
      firebase.auth().onAuthStateChanged(async function(fuser) {
        //user.value.displayName = fuser? fuser.displayName : "" // This can be used as an invoker?
        user.value = fuser? fuser : {displayName:null}
        _appState.userMeta.value = await controller.get_memberGMeta_by_keyValue("uid", fuser.uid).then(x=>x[0])
      });
    })
    const e_async_firebaseGSignin = async ()=>{
      // if(!popDialogue_display.value.value){
      //   await (popDialogue_display.value.value = true) // !popDialogue_display.value.value
      //   console.log('bbbb')
      // }

    }
    const e_firebaseGSignin = ()=>{
      
      if(!popDialogue_display.value.value){
        popDialogue_display.value.value = true // !popDialogue_display.value.value
      }
      return
      firebaseGSignin()
    }
    const click_droptoggle1 = ()=>{
      droptoggle1.value = !droptoggle1.value
      dropwrapper_on.value = !dropwrapper_on.value
    }
    
    
    const tlholder_add_open = ()=>{
      _appState.tlHolderBusy.value = true
      document.querySelector("#tl-holder").classList.add("on")
    }
    const tlholder_edit_open = ()=>{
      _appState.tlHolderBusy.value = true
      document.querySelector("#tl-holder").classList.add("on")
    }

    const dc_tlholder_add_open = (key)=>{
      return ()=>{
        if(!_appState.popupInstance.value){
          return
        }
        
        for(let _key in _appState.popupInstance.value){
          _appState.popupInstance.value[_key]["toggle"] = false
        }
        _appState.popupInstance.value[key]["toggle"] = true
        tlholder_add_open()
      }
    }
    const dc_tlholder_edit_open = (key)=>{
      return ()=>{
        if(!_appState.popupInstance.value){
          return
        }
        
        for(let _key in _appState.popupInstance.value){
          _appState.popupInstance.value[_key]["toggle"] = false
          delete _appState.popupInstance.value[_key]["title_temp"] 
        }
        
        _appState.popupInstance.value[key]["toggle"] = true
        _appState.popupInstance.value[key]["title_temp"] = ""
        tlholder_edit_open()
      }
    }
    const e_signout =  ()=>{
      firebaseGSignout()
      location.reload()
    }

    const click_outside = ()=>{
      if(droptoggle1.value){// if open
        click_droptoggle1()
      }
      
    }
    const userLoggedIn = computed({
      get: () => { 
        return user.value.displayName && !(user.value.displayName===null) 
      }
    })
    const userLoggedIn__sameChannel = computed({
      get: () => { 
        return (user.value.displayName && !(user.value.displayName===null)) && (user.value.uid == _appState.due_channel.value.uid)
      }
    })
    //_appState.userLoggedIn__sameChannel = userLoggedIn__sameChannel
    return {
      appState:_appState,
      proxy_reverse: _appState.utils.proxy_reverse,
      user: user,
      e_firebaseGSignin: e_firebaseGSignin,
      droptoggle1:droptoggle1,
      click_droptoggle1:click_droptoggle1,
      dropwrapper_on:dropwrapper_on,
      tlholder_add_open_schema_space_list: dc_tlholder_add_open("schema_space_list"),
      tlholder_edit_open_schema_space_list: dc_tlholder_edit_open("schema_space_list"),
      tlholder_add_open_schema_channel_list: dc_tlholder_add_open("schema_channel_list"),
      e_signout,
      click_outside,
      //pathname: _appState.pathname ,
      href,
      userLoggedIn,
      userLoggedIn__sameChannel,
      popDialogue_display
    }

  },
  methods: {
    
  }
}
</script>

