<template>
    <div class="e-v-input-root" ref="root">
        <label class="e-v-input-label text-stretch-after">{{htmlLabel}}</label>
        <select :class="[{'e-v-input':true},`e-v-input-${inputkey}`]" :data-key="inputkey" v-model="strVal" @change="onChange">
          <option v-if="defaultFill" value="-1"></option>
          <option v-for="(x, ii) in inputOptions" :value="x.value" > 
            <!-- defaultFill&&ii==0 -->
            {{x.label}}
          </option>
        </select>
        <div class="e-v-input-inc_right"></div>
    </div>
</template>

<script>
import {ref} from "vue"
import appState from '@/modules/appState.js'


export default {
  name: 'e-v-input-select',
  props: {
    inputkey: String,
    inputlabel: String,
    inputOptions: Array,
    label: String,
    onChange: Object,
    defaultFill:Boolean,
    default: String
  },
  async setup(props,context) {
      const root = ref(null)
      const _appState = appState()
      const strVal = ref("")
      const inputOptions = ref(props.inputOptions)
      const htmlLabel = ref((props.inputlabel??props.inputkey)||"htmlLabel")
      strVal.value = props.default?props.default:""
      return {
          onChange:(ev)=>{
            console.log("change")
            let context = {}
            context.value = ev.target.value
            props.onChange(props, context)
          },
          root,
          strVal,
          inputOptions,
          defaultFill:props.defaultFill,
          htmlLabel
      }
  }
}
</script>

